.Hangman {
  width: 400px;
  color: black;
  text-align: center;
  zoom: 1.3;
}

.Hangman img {
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
  height: 200px;
}

.Hangman h1 {
  font-size: 2rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 10px;
}

.total-guesses {
  margin-bottom: -10px;
  margin-top: 10px;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0em 0em;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 25px;
  height: 25px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #ffc107;
  color: #fff;
  -webkit-box-shadow: 0 6px #ff9800;
          box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
}

#restart-button {
  -webkit-box-shadow: none;
          box-shadow: none;
  border-radius: none;
  padding-top: none;
  width: auto;
  margin: 0 auto;
  background: red;
  display: block;
}

#restart-button:hover {
  background-color: black;
}

.Hangman button:hover {
  background-color: #ff9800;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px #9e9e9e;
          box-shadow: 0 6px #9e9e9e;
}



